.NavigationItem {
	list-style: none;
		width: 100%;
		padding-left: 10px;
		margin:10px 0;

		box-sizing: border-box;
		display: block;
		
}


.NavigationItem a {
	color:#8f5c2c;
	text-decoration: none;
	width: 100%;
	
	box-sizing: border-box;
	display: block;
}


.NavigationItem a:hover,
.NavigationItem a.active,
.NavigationItem a:active {
	
	color:#40a4c8;
}



@media (min-width: 500px) {
	.NavigationItem {
	list-style: none;
		height: 100%;
		padding-left: 10px;
		margin:0;
		display: flex;
		align-items: center; 
		width: auto;
}


.NavigationItem a {
	color:white;
	text-decoration: none;
	height: 100%;
	padding: 16px 10px;
	border-bottom: 4px solid transparent;
}


.NavigationItem a:hover,
.NavigationItem a.active,
.NavigationItem a:active {
	background-color: #8f5c2c;
	border-bottom: 4px solid #40a4c8;
	color:white;
}


}