.BuildControl {
	width:40%;
	display: flex;
    justify-content: center;
	align-items: center;
	margin: 0 auto;
}

.BuildControl button {
	display: block;
	padding: 5px;
	margin:0 5px;
	width: 30%;
	border:1px solid #AA6817 ;
	cursor: pointer;
	outline: none;
}

.BuildControl button:disabled {
    background-color: #AC9980;
    border: 1px solid #7E7365;
    color: #ccc;
    cursor: default;
}

.BuildControl button:hover:disabled {
    background-color: #AC9980;
    color: #ccc;
    cursor: not-allowed;
}

.Label {
    padding: 10px;
    font-weight: bold;
    width: 80px;
}

.BuildControl .Less {  
    background-color: #D39952;
    color: white;
}

.BuildControl .More {
    background-color: #8F5E1E;
    color: white;
}

.BuildControl .Less:hover, .BuildControl .Less:active {  
    background-color: #DAA972;
    color: white;
}

.BuildControl .More:hover,.BuildControl .More:active {
    background-color: #99703F;
    color: white;
}

@media (max-width: 499px){
    .BuildControl {
    width:auto;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
}
}