.BuildControls {
	width: 100%;
	background-color: #CF8F2E;
	display: flex;
	flex-flow: column;
	text-align: center;
	box-shadow: 0 2px 1px #ccc;
	margin:auto;
	padding: 10px 0;
}


.Button {
	width:40%;
	margin:0 auto;
    background-color: #DAD735;
    outline: none;
    cursor: pointer;
    border: 1px solid #966909;
    color: #966909;
    font-family: inherit;
    font-size: 1.2em;
    padding: 15px 30px;
    box-shadow: 2px 2px 2px #966909;
}

.Button:hover, .Button:active {
    background-color: #A0DB41;
    border: 1px solid #966909;
    color: #966909;
}

.Button:disabled {
    background-color: #C7C6C6;
    cursor: not-allowed;
    border: 1px solid #ccc;
    color: #888888;
}

.Button:not(:disabled) {
    animation: enable 0.3s linear;
}

@keyframes enable {
    0% {
        transform: scale(1);
    }
    60% {
        transform: scale(1.1);
    }
    100% {
        transform: scale(1);
    }
}

@media (max-width: 499px) {
    .BuildControls {
    width: auto;
    background-color: #CF8F2E;
    display: flex;
    flex-flow: column;
    text-align: center;
    justify-content: center;
    align-items:center;
    box-shadow: 0 2px 1px #ccc;
    margin:auto;
    padding: 10px 0;
}
}